html, body, #app {
	height: 100%;
	background: #F4F4F4;
	font: 14px/1.21 Roboto, 'Helvetica Neue', arial, sans-serif;
	overflow: hidden;
}

.fill() {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}




.stream {
	.fill();
	overflow: hidden;

	.posts {
		.fill();
		height: auto;
		bottom: 60px;
		overflow: auto;
		overflow-scrolling: touch;
		-webkit-overflow-scrolling: touch;

		& > .post-inner {
			overflow: hidden;
		}

		.post {
			padding: 10px;
			overflow: hidden;

			.avatar {
				float: right;
				width: 36px;
				height: 36px;
				margin: 0 7px;
				background-color: #CCC;
				background-position: center;
				background-size: contain;
				background-repeat: no-repeat;
				border-radius: 18px;
			}

			p {
				margin: 0;
				padding: 10px;
				background-color: #FFF;
				color: #000;
				overflow: hidden;
			}

			// &.from-them {
